@import url(https://fonts.googleapis.com/css2?family=Work+Sans:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
    box-sizing:border-box;
}

.AppContainer{
    display:flex;
    flex-direction: column;
    align-content: center;
    justify-content: center;
    text-align: center;
    font-family: 'work Sans'
    
}

.searchBar{
    display: flex;
    align-items: center;
    flex-direction: column;
    margin-top: 20px;
    width: 300px;
    height: 40px;
    font-size: 20px;
    padding:10px;
    margin: 20px;
    justify-content: center;
    align-content: center;
}
h1{
    
    align-content: center;
    align-items:center;
    text-align: center;
}
p{
    margin: 10px;
    font-size: 18px
}
.Card img{
    height:100%;
    width:100%;
    padding:0px 0px;
    margin: 0px 0px;
}

h4{
    background-color:#A7B5C7;
    display: flex;
    flex-direction: column;
    align-content: center;
    align-items: center;
    padding: 10px;
    font-size: 14px;
    height: 15px;
    text-align: center;
    justify-content: center;
    margin: 0px;
}
.grid-responsive{
    display:grid;
    grid-template-columns: repeat(auto-fit,minmax(360px,1fr));
    grid-gap:1rem;
    gap:1rem; 
}

.cardsContainer{
    display:grid;
    grid-template-columns: repeat(auto-fit,minmax(360px,1fr));
    grid-gap:1rem;
    gap:1rem; 
    background-color: #B5E5EB;
    padding-top:30px;
    }
    
    .Card{
        height: 20%;
        margin: 0px;
        padding:0px;
    
    }
    
    
    
    
    h4{
        background-color:#A7B5C7;
        display: flex;
        flex-direction: column;
        align-content: center;
        align-items: center;
        padding: 10px;
        font-size: 14px;
        height: 15px;
        text-align: center;
        justify-content: center;
        margin: 0px;
    }
    

